@import url(https://fonts.googleapis.com/css2?family=Ephesis&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;500&display=swap);
.body {
}
.name {
	height: 50vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.mainname {
	/* background-color: aqua; */
	color: aliceblue;
	margin-top: 10%;
	font-family: 'Ephesis', 'Courier New', Courier, monospace;
	font-size: 15rem;
	font-weight: 600;
	letter-spacing: 0.4rem;
	transition: 400ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
.other {
	/* background: #000; */
	height: 4rem;
	color: aliceblue;
	margin-top: -3rem;
	display: flex;
	justify-content: space-around;
	align-items: center;
}
.other a {
	margin-left: 1rem;
	font-size: 2rem;
	text-decoration: none;
	color: aliceblue;
}
.mainname:hover {
	color: rgb(226, 75, 75);
	cursor: pointer;
}
.game:hover {
	color: rgb(226, 75, 75);
	font-size: 2.4rem;
	cursor: pointer;
}
.web:hover {
	/* color: rgb(60, 195, 7);
	 */
	color: rgb(75, 226, 75);
	font-size: 2.4rem;
	cursor: pointer;
}
.dev:hover {
	color: rgb(226, 75, 75);
	font-size: 2rem;
	cursor: pointer;
}
.other a {
	transition: 200ms ease-in-out;
}
@media (max-width: 900px) {
	.mainname {
		font-size: 8rem;
	}

	@media (max-width: 650px) {
		.mainname {
			font-size: 8rem;
		}
		.other {
			margin-top: -2rem;
		}
	}
	@media (max-width: 500px) {
		.mainname {
			font-size: 6rem;
		}
		.other {
			margin-top: -1rem;
		}
	}
}
@media (max-width: 350px) {
	.mainname {
		font-size: 4rem;
	}
	.other {
		margin-top: -0.5rem;
	}
}

html {
	font-size: 62.5%;
}
* {
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
	font-family: 'Poppins', 'Courier New', Courier, monospace;
}

.navbar {
	/* background: #334756; */
	/* background-image: linear-gradient(-147deg, #000000 0%, #2c3e50 74%); */
	background-color: #2d3436;
	background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
	height: 5rem;
	display: flex;
	justify-content: space-around;
	align-items: center;
}
.logo {
	font-size: 4rem;
	font-weight: 900;
	letter-spacing: 0.6rem;
	color: rgb(214, 214, 214);
	transition: 300ms;
}
.navlinks {
	display: flex;
	width: 30%;
	justify-content: space-around;
	transition: 200ms ease-in;
}
.navlinks li {
	/* background: #000; */
	padding: 1rem;
	border-radius: 5px;
	list-style: none;
	transition: 200ms ease-in-out;
}
.navlinks a {
	text-decoration: none;
	font-size: 1.5rem;
	color: rgb(230, 230, 230);
	transition: 200ms ease-in-out;
}

.navlinks li:hover {
	background-color: rgb(226, 75, 75);
}
/* hover */
.logo:hover {
	cursor: pointer;
	color: rgb(226, 75, 75);
}

@media (max-width: 700px) {
	.navlinks li {
		margin-right: 1rem;
	}
	.navlinks {
		width: 50%;
	}
}
@media (max-width: 600px) {
}
@media (max-width: 450px) {
	.navlinks {
		width: 50%;
		/* background-color: rgb(24, 106, 179); */
	}
	.navlinks li {
		/* margin-right: 0.5rem; */
		padding: 0.5rem;
	}
	.navlinks a {
		font-size: 1rem;
	}
	.logo {
		font-size: 3rem;
	}
}
@media (max-width: 300px) {
	.navlinks li {
		/* margin-right: 0.5rem; */
		padding: 0rem;
	}
	.navlinks a {
		font-size: 1rem;
	}
	.logo {
		font-size: 2rem;
	}
}

.main {
	height: 100vh;
	width: 100wh;
	background-image: linear-gradient(147deg, #000000 0%, #2c3e50 74%);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.end {
	background: rgb(0, 0, 0);
	height: 9rem;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.end div {
	color: wheat;
	font-size: 1rem;
	/* line-break: auto; */
	text-align: center;
}
.first {
	color: wheat;
	margin: 1rem 0rem 1rem;
	font-size: 1.5rem;
	letter-spacing: 1rem;
}
@media (max-width: 500px) {
	.end {
		height: 12rem;
	}
}

